.last {
    &__lastPageDescr {
        width: 60%;
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 36px;
        line-height: 46px;
        margin-top: 80px;
        margin-left: 100px;
    }

    &__wait {
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 36px;
        font-weight: 700;
        margin-top: 80px;
        margin-bottom: 200px;
        margin-left: 100px;
    }

    &__advice {
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 20px;
    }

    &__footer {
        position: relative;
        display: flex;
        left: -100;
    }

    &__mail {
        width: 293px;
        height: 22px;
        color: #000000;
        font-family: $font-family-sans-serif;
        font-size: 20px;
        font-weight: 700;
        margin-left: 20px;
    }

    &__rocket {
        position: absolute;
        top: 228px;
        left: 0;
    }
}
